export default {
  layout: "Adm-content",
  data() {
    return {
      forms: "",
    };
  },
  mounted() {
    this.availableForm();
  },

  methods: {
    availableForm() {
      this.$axios.$get("/admission/form-stock").then((response) => {        
        this.forms = response.data;
      });
    },
  },
};
